<template>
  <div>
  <el-dialog
    :title="(openType==1?'新增':'编辑')+'收货单'"
    :visible.sync="isVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :lock-scroll="true"
    width="95%"
    top="5vh"
    class="heightDialog"
    :before-close="cancel"
    @close="cancel">
  <el-form ref="orderInfo" :model="orderInfo" :rules="orderFormRules" label-width="60px">
    <el-row>
      <el-col :span="5">
        <el-form-item label="商户" prop="goodsReceiveMerchantId" >
          <el-select v-model="orderInfo.goodsReceiveMerchantId" placeholder="请选择商户" filterable @change="changeMerchants">
            <el-option
              :disabled="item.disabled"
              v-for="(item,index) in merchantsList"
              :key="index"
              :label="item.userName"
              :value="item.userUuid"
            />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="5" class="">
        <el-form-item label="类型" prop="goodsReceiveType" >
          <el-select v-model="orderInfo.goodsReceiveType" class="w100p" placeholder="请选择收货单类型">
            <el-option class="radio" value="1" label="采购入库"></el-option>
            <el-option class="radio" value="2" label="搬仓入库"></el-option>
            <el-option class="radio" value="3" label="提货入库"></el-option>
            <el-option class="radio" value="4" label="售后入库"></el-option>
            <el-option class="radio" value="5" label="展厅下样入库"></el-option>
            <el-option class="radio" value="6" label="展厅撤场入库"></el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="7">
        <el-form-item label="送达日期" prop="goodsReceiveDate" label-width="90px">
          <el-date-picker
            class="w100p"
            v-model="orderInfo.goodsReceiveDate"
            align="right"
            type="date"
            placeholder="选择日期"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
      </el-col>
      <el-col :span="7">
        <el-form-item label="备注" prop="goodsReceiveRemark" >
          <el-input v-model="orderInfo.goodsReceiveRemark" type="textarea" :autosize="{maxRows:3}" maxlength="200" resize="none" class="create_textarea" placeholder="请输入收货单备注，最长200字符" />
        </el-form-item>
      </el-col>
    </el-row>
    <!--<el-row>-->
      <!--<el-col :span="5" :offset=19 class="text-r pad-r-10">-->
        <!--<el-button @click="uploadProd">批量导入</el-button>-->
      <!--</el-col>-->
    <!--</el-row>-->
    <el-row>
      <div>
      <common-table :flex="300">
        <template slot="table">
          <el-table
            ref="orderTable"
            height="100%"
            stripe
            :border="true"
            v-loading="isListLoading"
            :data="tableData">
            <el-table-column type="index" label="序号" width="50" fixed="left" header-align="center"></el-table-column>
            <el-table-column label="品牌" prop="productBrand"></el-table-column>
            <el-table-column label="型号" prop="productModel"></el-table-column>
            <el-table-column label="货品名称" prop="productName"></el-table-column>
            <el-table-column label="规格" prop="productStandard"></el-table-column>
            <el-table-column label="颜色" prop="productColor"></el-table-column>
            <el-table-column label="货品类型" header-align="center" min-width="110">
            <template slot-scope="scope">
            <el-select v-model="scope.row.productType" :disabled="scope.row.typeDisabled" placeholder="请选择">
            <el-option value="0" label="小件"></el-option>
            <el-option value="1" :disabled="true" label="套件"></el-option>
            <el-option value="2" label="单品"></el-option>
            </el-select>
            </template>
            </el-table-column>
            <el-table-column label="包装形式" header-align="center" min-width="110" :formatter="PackFormatter">
            <template slot-scope="scope">
            <el-select v-model="scope.row.receiveItemPack" :disabled="openType!='1'" placeholder="请选择">
            <el-option
            v-for="(item,index) in packagedType"
            :key="index"
            :label="item.label"
            :value="item.value">
            </el-option>
            </el-select>
            </template>
            </el-table-column>
            <el-table-column label="子件" min-width="120" header-align="center">
            <template slot-scope="scope">
              <el-tooltip v-if="scope.row.productItemUuid && scope.row.productType == '1'" placement="top" effect="light" enterable :visible-arrow="false">
                <div slot="content" >
                  <div>
                    {{getItemName(scope.row.productItemUuid)}}
                  </div>
                </div>
                <el-select v-model="scope.row.productItemUuid" @change="changeModel(scope.row,scope.$index)" v-if="scope.row.productType == '1' ">
                  <el-option
                    :disabled="false"
                    v-for="(item,index) in scope.row.productItems"
                    :key="index"
                    :label="item.productItemName"
                    :value="item.productItemUuid">
                  </el-option>
                </el-select>
              </el-tooltip>
              <el-select v-if="!scope.row.productItemUuid && scope.row.productType == '1'" v-model="scope.row.productItemUuid" @change="changeModel(scope.row,scope.$index)">
                <el-option
                  :disabled="item.disabled"
                  v-for="(item,index) in scope.row.productItems"
                  :key="index"
                  :label="item.productItemName"
                  :value="item.productItemUuid">
                </el-option>
              </el-select>
            </template>
            </el-table-column>
            <el-table-column label="数量" header-align="center" min-width="110">
            <template slot-scope="scope">
            <el-input-number v-model="scope.row.receiveItemCount" class="productCount" controls-position="right" :min="1" :max="99999" size="mini"></el-input-number>
            </template>
            </el-table-column>
            <el-table-column label="备注" min-width="140" header-align="center">
            <template slot-scope="scope">
            <el-input v-model="scope.row.receiveItemRemark" type="textarea" :autosize="{maxRows:2}" maxlength="200" resize="none" class="create_textarea" placeholder="最长200字符"></el-input>
            </template>
            </el-table-column>
            <el-table-column
              :render-header="renderHeader"
              fixed="right"
              width="100">
              <template slot-scope="scope">
                <el-button
                  @click.native.prevent="deleteRow(scope.$index, scope.row)"
                  type="text"
                  rules="small">
                  删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </template>
      </common-table>
        </div>
    </el-row>
    <el-row>
      <el-col :span="5" :offset=19 class="text-r mar-t-10">
        <el-button type="primary" :loading="isSaveing" @click="save()">提交</el-button>
        <el-button @click="cancel()">取消</el-button>
      </el-col>
    </el-row>
  </el-form>
  </el-dialog>
    <!-- 新增模板-->
    <template-create
      class="highIndex"
      :show.sync="isCreateTemplateshow"
      :searchParams="searchParams"
      :merchantsList="merchantsList"
      :merchants="orderInfo.goodsReceiveMerchantId"
      :brandList="brandList"
      :warehouseList="warehouseList"
      @sendDataOut="receiveData"
      @reset="resetRow"
    />
    <!-- 导入订单 -->
    <template-import
      :show.sync="importVisible"
      :merchantsList = "merchantsList"
      @updateList="queryList"
    />
    <template-select
      :show.sync="isTemplateShow"
      :merchantsList="merchantsList"
      :merchantId="orderInfo.goodsReceiveMerchantId"
      @sendData="receiveData"
    />


  </div>
</template>

<script>
import CommonTable from '@/components/commonTable'
import TemplateCreate from '@/views/Development/ProductTemplate/Components/TemplateCreate'
import TemplateImport from '@/views/Development/ProductTemplate/Components/ImportTemplate'
import TemplateSelect from './TemplateSelect'
import { getAllProducetTemplateList,uploadTemplate } from '@/api/warehouse'
import { getEntryOrderDetail, updateEntryOrder, deleteEntryOrder } from '@/api/order'
import { getAllMerchantsList ,getBrandModel } from '@/api/common'
import { setSession, getSession } from '@/utils/session'
import QrCode from './QrCode'
const InitProduct = {
  receiveItemPack: '1',
  receiveItemCount: '1'
}
export default {
  name: 'OrderCreate',
  data () {
    return {
      originalData: {},
      isSaveing:false,
      createIndex:'',
      isCreateTemplateshow: false,
      isVisible: false,
      orderInfo: {},
      isListLoading: false,
      packagedType: [{
        value: '1',
        label: '有包装'
      }, {
        value: '2',
        label: '无包装'
      }, {
        value: '3',
        label: '其他'
      }],
      originModel: [],
      existModel: [],
      modelBrand: [],
      brandList: [],
      modelList: [],
      tableData: [],
      orderFormRules: {
        goodsReceiveMerchantId: [
          {required: true, message: '请选择商户', trigger: 'blur'},
        ],
        goodsReceiveType: [
          {required: true, message: '请选择订单类型', trigger: 'blur'},
        ],
        goodsReceiveDate: [
          {required: true, message: '请选择送达时间', trigger: 'blur'}
        ]
      },
      importVisible: false,
      hasSaved: false,
      selectIndex: '',
      isTemplateShow: false
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    openType: {
      type: Number,
      default: 1
    },
    selectedOrderInfo: {
      type: Object
    },
    warehouseList: {
      type: Array
    },
    merchantsList: {
      type: Array
    },
    searchParams: {
      type:Object
    }
  },
  watch: {
    show (isShow) {
      if(isShow) {
        if (this.openType === 2 || this.openType === 3) {
          this.originalData = Object.assign({},this.selectedOrderInfo)
          this.orderInfo = Object.assign({}, this.selectedOrderInfo)
          // this.getTemplate().then(() => {.
          this.orderInfo.goodsReceiveItems.forEach(item => {
            item.productUuid = item.receiveItemProductId
          })
          this.tableData = [].concat(this.orderInfo.goodsReceiveItems)
          this.tableData.forEach(item => {
            if(item.productType != ''){
              item.typeDisabled = true
            }
            this.changeModel(item)
          })
        } else {
          this.tableData = []
        }
      }
      this.isVisible = isShow
    }
  },
  methods: {
    changeMerchants(value){
      this.tableData = []
      this.existModel = []
      this.originModel = []
      this.getBrandModel({warehouseUuid:getSession('warehouseUuid'),merchantUuid:value})
    },
    // 获取品牌型号列表
    getBrandModel (params) {
      getBrandModel(params).then(res => {
        if (!res.error) {
          this.brandList = []
          this.modelList = []
          res.forEach((item,index) => {
            this.brandList.push(item.productBrand)
            item.productModel.forEach(item2 => {
              this.modelList.push(item2)
            })
          })
          this.modelBrand = res
        }
      })
    },
    // 获取模板列表model
    getTemplate () {
      return new Promise(resolve => {
        let params = {
          merchantUuid: this.orderInfo.goodsReceiveMerchantId
        }
        this.isListLoading = true
        getAllProducetTemplateList(params).then(res => {
          this.isListLoading = false
          this.originModel = []
          this.originModel = this.originModel.concat(res)
          for (let item of this.originModel) {
            delete item.descOrAsc
            delete item.orderBy
            delete item.pageNum
            delete item.pageSize
            delete item.productItemUuid
            item.disabled = false
            if (item.productType == '') {
              item.typeDisabled = false
            } else {
              item.typeDisabled = true
            }
            if(item.productType == '1' && item.productItems.length){
              item.productItems.forEach(item2 => {
                item2.disabled = false
                item.productItemUuid = item.productItems[0].productItemUuid
              })
            }
          }
          this.originModel.unshift({productUuid:'新增',productModel:'新增'})
          resolve()
        })
      })
    },
    renderHeader (createElement, {}) {
      return createElement(
        'el-button', {
          'class': ['addBtn','el-button--primary'],
          domProps: {
            innerHTML: '添加货品'
          },
          on: {
            click: () => { this.addRow() },
          }
        })
    },
    addRow () {
      if(!this.orderInfo.goodsReceiveMerchantId){
        this.$message({
          type: 'warning',
          message: '请选择商户'
        })
        return
      }
      this.isTemplateShow = true
      this.selectIndex = this.tableData.length
    },
    deleteRow (index, row) {
      if(row.productType == '1' && row.productItems && row.productItems.length){
        this.tableData.forEach(item => {
          if(item.productUuid == row.productUuid){
            item.productItems.forEach(item2 => {
              if(item2.productItemUuid == row.productItemUuid) {
                item2.disabled = false
              }
            })
          }
        })
      }
      this.tableData.splice(index, 1)
    },
    receiveData (data) {
        data.forEach(item => {
          if (item.productType !== '') {
            item.typeDisabled = true
          }
          item = Object.assign(item, InitProduct)
          if (item.productType == '1' && item.productItems && item.productItems.length) {
            // let existProd = this.tableData.filter(item2 => {
            //   return item2.productUuid == item.productUuid
            // })
            // if(existProd){
            //   item.productItems.forEach(item2 => {
            //     let existItem = existProd.find(item3 => {
            //       return item3.productItemUuid == item2.productItemUuid
            //     })
            //     if (!existItem) {
            //       item.productItemUuid = item2.productItemUuid
            //       item2.disabled = true
            //       let prod = Object.assign({},item)
            //       this.$set(this.tableData,this.selectIndex,prod)
            //       this.changeModel(prod)
            //       this.selectIndex++
            //     }
            //   })
            // }else{
              item.productItems.forEach(item2 => {
                item.productItemUuid = item2.productItemUuid
                item2.disabled = true
                let prod = Object.assign({},item)
                this.tableData.unshift(prod)
                // this.$set(this.tableData,this.selectIndex,prod)
                this.changeModel(prod)
                this.selectIndex++
              })
            // }
          } else if (item.productType != '1') {
            // let itemResult = this.tableData.find(item3 => {
            //   return (item3.productUuid == item.productUuid)
            // })
            // if (!itemResult) {
              let prod = Object.assign({},item)
            this.tableData.unshift(prod)
            // this.$set(this.tableData,this.selectIndex,prod)
              this.selectIndex++
            }
          // }
        })
    },
    resetRow (hasSave) {
      if(!hasSave){
        InitProduct.productUuid = ''
        this.$set(this.existModel,this.createIndex,this.originModel)
        this.$set(this.tableData,this.createIndex,InitProduct)
      }
    },
    cancel () {
      if (!this.hasSaved && Object.is(this.originalData,this.orderInfo)) {
        this.$confirm('确认要取消编辑吗？', '提醒', {
          confirmButtonText: '确定',
          cancelButtonText: '继续编辑'
        }).then(() => {
          this.existModel = []
          this.originModel = []
          this.tableData =[]
          this.orderInfo = {}
          this.originalData = {}
          this.$refs.orderInfo.resetFields()
          this.$emit('update:show', false)
          this.isVisible = false
        }).catch(() => {})
      } else {
        this.existModel = []
        this.originModel = []
        this.tableData =[]
        this.orderInfo = {}
        this.originalData = {}
        this.$refs.orderInfo.resetFields()
        this.$emit('update:show', false)
        this.isVisible = false
      }
    },
    dealModel(brand,index){
      if(brand !== '' ){
        this.existModel = this.originModel.filter(item => {
          return item.productBrand === brand
        })
        this.existModel.unshift({productUuid:'新建',productModel:'新建'})
      } else if (brand === '') {
        this.existModel = [].concat(this.originModel)
      }
    },
    changeModel (row) {
      if(row.productType == '1' && row.productItems && row.productItems.length){
        let existItem = []
        let prodResult = this.tableData.filter(item => {
          return item.productUuid == row.productUuid
        })
        if(prodResult && prodResult.length){
          prodResult.forEach(item => {
            existItem.push(item.productItemUuid)
          })
        }
        this.tableData.forEach((item) => {
          if(item.productUuid == row.productUuid){
            item.productItems.forEach(item2 => {
              let result = existItem.find(item3 => {
                return item3 == item2.productItemUuid
              })
              if(!result){
                item2.disabled = false
              } else {
                item2.disabled = true
              }
            })
          }
        })
      }
    },
    changeItem (value,index) {
      let result = this.tableData.filter(item => {
        return item.productUuid === value.productUuid
      })
      if (result && result.length) {
          this.existModel.forEach(item => {
            item.forEach(item2 => {
              if (item2.productType === '1') {
                item2.productItems.forEach(item3 => {
                  let itemResult = result.find(item4 => {
                    return item4.productItemUuid === item3.productItemUuid
                  })
                  if(!itemResult){
                    item3.disabled = false
                  }else{
                    item3.disabled = true
                  }
                })
              }
            })
          })
      }
    },
    getItemName (id) {
      let result = this.tableData.find(item => {
        return item.productItemUuid == id
      })
      if(result){
        let itemName = result.productItems.find(item2 => {
          return item2.productItemUuid == id
        })
        if(itemName){
          return itemName.productItemName
        }
      }
    },
    save () {
      this.$refs.orderInfo.validate(isValid => {
          if (isValid) {
            if(!this.tableData.length){
              this.$message({
                message: `请补全货物信息`,
                type: 'warning'
              })
              return
            }
            for(let item of this.tableData){
              item['receiveItemProductId'] = item.productUuid
              delete item.productItems
              if(!item||!item.productUuid||item.productUuid === ''){
                this.$message({
                  message: `请补全货物信息`,
                  type: 'warning'
                })
                return
              }
              if (item.productType == '') {
                this.$message({
                  message: `请选择货品类型`,
                  type: 'warning'
                })
                return
              }
            }
            let params = {
              ...this.orderInfo,
              goodsWarehouseUuid: getSession('warehouseUuid'),
              goodsReceiveMerchant: this.merchantsList.find(item => item.userUuid === this.orderInfo.goodsReceiveMerchantId).userName,
              goodsReceiveItems: this.tableData
            }
            this.isSaveing = true
            updateEntryOrder(params).then(res => {
              this.isSaveing = false
              if (!res.error) {
                let submitText = this.openType == 1 ? '新增' : '编辑'
                this.$message({
                  message: `${submitText}成功!`,
                  type: 'success'
                })
                this.hasSaved = true
                this.cancel()
                this.queryList()
              }
            })
          }})
    },
    PackFormatter (row) {
      return row.receiveItemPack === '1' ? '有包装' : row.receiveItemPack === '0' ? '无包装' : '未知'
    },
    uploadProd () {
      this.importVisible = true
    },
    queryList () {
      this.$emit('search',this.searchParams)
    },
    printCode () {
      this.paramsList = []
      this.tableData.forEach(item => {
        this.paramsList.push(item.productUuid)
      })
      this.qrCodeVisible = true;
    },
    dealItemName (item) {
      let itemsName = ''
      if (item.productType == '1' && item.productItems.length) {
        itemsName = '子件:'
        item.productItems.forEach((item2,index) => {
          itemsName += item2.productItemName + (index === item.productItems.length-1 ? '' : '、')
        })
      } else if (item.productType == '0') {
        itemsName = '小件'
      } else if (item.productType == '2') {
        itemsName = '单品'
      }
      return itemsName
    }
  },
  components: {
    QrCode,
    CommonTable,
    TemplateCreate,
    TemplateImport,
    TemplateSelect
  }
}
</script>

<style lang="scss" scoped>
  .mar-l-60{
     margin-left: 60px;
   }
  .pad-r-10{
    padding-right: 10px;
  }
  .mar-t-10{
    margin-top: 10px;
  }
  .addBtn{
    width: 80px;
  }
  .productCount.el-input-number--mini{
    width: 100px !important;
    .el-input__inner {
      padding-left: 5px !important;
    }
  }
  .productInfo{
    height: 100%;
  }
  .itemsName{
    color: #8492a6;
    font-size: 13px;
    max-width: 300px;
    white-space: normal;
    word-break: break-all;
    word-wrap: break-word;
  }
</style>



